<template>
  <div class="apollo-example">
    <v-breadcrumbs
      v-if="!!isFiltered"
      :items="[
        {
          text: 'Companies',
          disabled: false,
          href: '/companies'
        },
        {
          text: `${this.$route.query.searchType} search`,
          disabled: true,
          href: '/companies'
        }
      ]"
      divider=">"
    ></v-breadcrumbs>
    <v-breadcrumbs
      v-else
      :items="[
        {
          text: 'Companies',
          disabled: false,
          href: '/companies'
        }
      ]"
      divider=">"
    ></v-breadcrumbs>
    <!--TODOLISTO: cambiar v-alert por v-snackbar -->
    <v-snackbar top v-model="snack" :timeout="3000" :color="snackColor">
      {{ snackText }}
      <v-btn flat @click="snack = false">Close</v-btn>
    </v-snackbar>
    <h1 v-if="!!isFiltered">You're currently filtering by</h1>
    <ul v-if="!!isFiltered">
      <li
        v-if="this.$route.query.simpleSearch"
      >Companies with the words {{this.$route.query.simpleSearch}} in the name or description</li>
      <li v-if="this.$route.query.name">Company name: {{this.$route.query.name}}</li>
      <li v-if="this.$route.query.country">Company country: {{this.$route.query.country}}</li>
      <li v-if="this.$route.query.website">Company website: {{this.$route.query.website}}</li>
      <li v-if="this.$route.query.city">Company city: {{this.$route.query.city}}</li>
      <li v-if="this.$route.query.region">Company region: {{this.$route.query.region}}</li>
      <li v-if="this.$route.query.state">Company state: {{this.$route.query.state}}</li>
      <li v-if="this.$route.query.status">Company status: {{this.$route.query.status}}</li>
      <li
        v-if="this.$route.query.lessThanEmployees"
      >Companies with less than {{this.$route.query.lessThanEmployees}} employees</li>
      <li
        v-if="this.$route.query.moreThanEmployees"
      >Companies with more than {{this.$route.query.moreThanEmployees}} employees</li>
    </ul>
    <div class="calltoactions">
      <v-btn color="primary" dark @click="toggleSearch">search</v-btn>
      <create-playlist-from-results v-if="isFiltered" @onSave="saveResultsAsPlaylist" />
      <create-signal-from-results v-if="isFiltered" @onSave="saveResultsAsSignal" />
    </div>
    <!-- Apollo watched Graphql query -->
    <template
      v-if="!!this.$route.query && !!this.$route.query.searchType && this.$route.query.searchType==='simple' && !!this.$route.query.simpleSearch"
    >
      <ApolloQuery
        :query="require('./graphql/CompaniesSimpleSearch.gql')"
        :variables="{ search: this.$route.query.simpleSearch, first: rowsPerPage, offset: (rowsPerPage * page) - rowsPerPage }"
      >
        <template slot-scope="{ result: { loading, error, data } }">
          <!-- Loading -->
          <div v-if="loading" class="loading apollo">Loading...</div>

          <!-- Error -->
          <!--<div v-else-if="error" class="error apollo">An error occured</div>-->

          <!-- Result -->
          <div v-else-if="data" class="result apollo">
            <!---<div>{{ JSON.stringify(data) }}</div>-->
            <companies-table
              v-if="data.companies"
              :items="data.companies"
              class="result apollo"
              @updatePagination="updatePagination"
            ></companies-table>
          </div>

          <!-- No result -->
          <div v-else class="no-result apollo">Loading...</div>
        </template>
      </ApolloQuery>
    </template>
    <template
      v-else-if="!!this.$route.query && !!this.$route.query.searchType && this.$route.query.searchType==='company'"
    >
      <ApolloQuery
        :query="require('./graphql/CompaniesAdvancedSearch.gql')"
        :variables="{ 
          name: this.$route.query.name || '',
          country: this.$route.query.country || '',
          website: this.$route.query.website || '',
          city: this.$route.query.city || '',
          region: this.$route.query.region || '',
          state: this.$route.query.state || '',
          status: this.$route.query.status || '',
          lessThanEmployees: this.$route.query.lessThanEmployees || '0',
          moreThanEmployees: this.$route.query.moreThanEmployees || '0',
          first: rowsPerPage,
          offset: (rowsPerPage * page) - rowsPerPage
          }"
      >
        <template slot-scope="{ result: { loading, error, data } }">
          <!-- Loading -->
          <div v-if="loading" class="loading apollo">Loading...</div>

          <!-- Error -->
          <!--<div v-else-if="error" class="error apollo">An error occured</div>-->

          <!-- Result -->
          <div v-else-if="data" class="result apollo">
            <!---<div>{{ JSON.stringify(data) }}</div>-->
            <companies-table
              v-if="data.companies"
              :items="data.companies"
              class="result apollo"
              @updatePagination="updatePagination"
            ></companies-table>
          </div>

          <!-- No result -->
          <div v-else class="no-result apollo">Loading...</div>
        </template>
      </ApolloQuery>
    </template>
    <template v-else>
      <ApolloQuery
        :query="require('./graphql/Companies.gql')"
        :variables="{first: rowsPerPage, offset: (rowsPerPage * page) - rowsPerPage}"
      >
        <template slot-scope="{ result: { loading, error, data } }">
          <!-- Loading -->
          <div v-if="loading" class="loading apollo">Loading...</div>

          <!-- Error -->
          <!--<div v-else-if="error" class="error apollo">An error occured</div>-->

          <!-- Result -->
          <div v-else-if="data" class="result apollo">
            <!---<div>{{ JSON.stringify(data) }}</div>-->
            <companies-table
              v-if="data.companies "
              :items="data.companies"
              class="result apollo"
              @updatePagination="updatePagination"
            ></companies-table>
          </div>

          <!-- No result -->
          <div v-else class="no-result apollo">Loading...</div>
        </template>
      </ApolloQuery>
    </template>
  </div>
</template>

<script>
import gql from "graphql-tag";
import _get from "lodash.get";
import CompaniesTable from "../../components/companies/CompaniesTable.vue";
import CreatePlaylistFromResults from "./components/CreatePlaylistFromResults.vue";
import CreateSignalFromResults from "./components/CreateSignalFromResults.vue";
export default {
  data() {
    return {
      items: ["Companies"],
      company: "",
      descending: false,
      page: 1,
      rowsPerPage: 5,
      sortBy: "",
      totalItems: 10,
      searchField: "",
      searchAdvance: {
        country: "",
        name: "",
        lessThanEmployees: 0,
        moreThanEmployees: 0,
        status: "",
        region: "",
        state: "",
        city: ""
      },
      isFiltered: false,
      snack: false,
      snackColor: "",
      snackText: ""
    };
  },
  components: {
    CompaniesTable,
    CreatePlaylistFromResults,
    CreateSignalFromResults
  },
  methods: {
    updatePagination({
      dataFromEvent: {
        descending = false,
        page = 1,
        rowsPerPage = 5,
        sortBy = "",
        totalItems = 10
      }
    }) {
      this.descending = descending;
      this.page = page;
      this.rowsPerPage = rowsPerPage;
      this.sortBy = sortBy;
      this.totalItems = 5;
    },
    toggleSearch() {
      this.$emit("toggleSearch", { show: !this.$props.showSearch });
    },
    checkIfIsFiltered() {
      let result = false;
      for (let key in this.$route.query) {
        console.log("key", key);
        if (!!this.$route.query[key] && key !== "searchType") {
          result = true;
          break;
        }
      }
      return result;
    },
    async saveResultsAsPlaylist(newPlaylistName = null) {
      console.log(
        "companies ",
        "saveResultsAsPlaylist ",
        "newPlaylistName =",
        newPlaylistName
      );
      if (!this.checkIfIsFiltered()) {
        this.snack = true;
        this.snackColor = "error";
        this.snackText =
          "At least one company field search criteria is not empty!";
        return;
      }
      if (!newPlaylistName) {
        this.snack = true;
        this.snackColor = "error";
        this.snackText = "New Playlist Name can not be empty!";
        return;
      }
      if (this.checkIfIsFiltered() && !!newPlaylistName) {
        try {
          const result = await this.$apollo.mutate({
            mutation: gql`
              mutation(
                $name: String
                $city: String
                $state: String
                $region: String
                $country: String
                $status: String
                $lessThanEmployees: Int
                $moreThanEmployees: Int
                $playlistName: String!
              ) {
                createPlaylistFromSearch(
                  companySearch: {
                    name: $name
                    city: $city
                    state: $state
                    region: $region
                    country: $country
                    status: $status
                    lessThanEmployees: $lessThanEmployees
                    moreThanEmployees: $moreThanEmployees
                  }
                  playlistData: { name: $playlistName }
                ) {
                  playlist {
                    uid
                    name
                  }
                }
              }
            `,
            // Parameters
            variables: {
              name: _get(this.$route.query, "name", ""),
              country: _get(this.$route.query, "country", ""),
              website: _get(this.$route.query, "website", ""),
              city: _get(this.$route.query, "city", ""),
              region: _get(this.$route.query, "region", ""),
              state: _get(this.$route.query, "state", ""),
              status: _get(this.$route.query, "status", ""),
              lessThanEmployees: _get(
                this.$route.query,
                "lessThanEmployees",
                "0"
              ),
              moreThanEmployees: _get(
                this.$route.query,
                "moreThanEmployees",
                "0"
              ),
              playlistName: newPlaylistName
            }
          });
          console.log("saving results as playlist success", result);
          const playlist = _get(
            result,
            "data.createPlaylistFromSearch.playlist",
            null
          );
          if (!playlist) {
            this.snack = true;
            this.snackColor = "error";
            this.snackText = "it seems that we created your playlist but couldn't check it, please check manually";
            return;
          }
          this.$router.push({
            path: `/playlists/${playlist.uid}/companies`
          });
        } catch (error) {
          this.snack = true;
          this.snackColor = "error";
          this.snackText = "oops we did something wrong!";
          console.log("error saving simple search as a play list", error);
        }
      }
    },
    async saveResultsAsSignal(signal = null) {
      if (!this.checkIfIsFiltered()) {
        this.snack = true;
        this.snackColor = "error";
        this.snackText =
          "At least one company field search criteria is not empty!";
        return;
      }
      if (!signal) {
        this.snack = true;
        this.snackColor = "error";
        this.snackText = "Please fill the signal form!";
        return;
      }
      console.log("signal", signal);
      if (this.checkIfIsFiltered() && !!signal) {
        const signalName = _get(signal, "name", "");
        const signalDescription = _get(signal, "description", "");
        const signalGroup = _get(signal, "group", "");
        const signalDefaultScore = parseFloat(
          _get(signal, "defaultScore", "0")
        );
        console.log("signal after if", {
          signalName,
          signalDescription,
          signalGroup,
          signalDefaultScore
        });
        try {
          const result = await this.$apollo.mutate({
            mutation: gql`
              mutation(
                $name: String
                $city: String
                $state: String
                $region: String
                $country: String
                $status: String
                $website: String
                $lessThanEmployees: Int
                $moreThanEmployees: Int
                $signalName: String
                $signalDescription: String
                $signalGroup: String
                $signalDefaultScore: Float
              ) {
                createSignalFromSearch(
                  companySearch: {
                    name: $name
                    city: $city
                    state: $state
                    region: $region
                    country: $country
                    status: $status
                    lessThanEmployees: $lessThanEmployees
                    moreThanEmployees: $moreThanEmployees
                    website: $website
                  }
                  signalData: {
                    name: $signalName
                    description: $signalDescription
                    defaultScore: $signalDefaultScore
                    group: $signalGroup
                  }
                ) {
                  signal {
                    id
                    name
                  }
                }
              }
            `,
            // Parameters
            variables: {
              name: _get(this.$route.query, "name", ""),
              country: _get(this.$route.query, "country", ""),
              website: _get(this.$route.query, "website", ""),
              city: _get(this.$route.query, "city", ""),
              region: _get(this.$route.query, "region", ""),
              state: _get(this.$route.query, "state", ""),
              status: _get(this.$route.query, "status", ""),
              lessThanEmployees: _get(
                this.$route.query,
                "lessThanEmployees",
                "0"
              ),
              moreThanEmployees: _get(
                this.$route.query,
                "moreThanEmployees",
                "0"
              ),
              signalName: signalName,
              signalDescription: signalDescription,
              signalGroup: signalGroup,
              signalDefaultScore: signalDefaultScore
            }
          });
          console.log("saving results as signal success", result);
          const signal = _get(
            result,
            "data.createSignalFromSearch.signal",
            null
          );
          if (!signal) {
            this.snack = true;
            this.snackColor = "error";
            this.snackText = "it seems that we created your signal but couldn't check it, please check manually";
            return;
          }
          console.log("finish");
          this.$router.push({
            path: `/signals/${signal.id}`
          });
        } catch (error) {
          this.snack = true;
          this.snackColor = "error";
          this.snackText = "oops we did something wrong!";
          console.log("error saving search results as signal", error);
        }
      }
    }
  },
  props: {
    showSearch: { type: Boolean, default: false }
  },
  beforeMount() {
    this.isFiltered = this.checkIfIsFiltered();
  },
  beforeUpdate() {
    this.isFiltered = this.checkIfIsFiltered();
  },
  updated() {
    this.isFiltered = this.checkIfIsFiltered();
  }
};
</script>

